<template>
    <figure>
        <img v-if="imgSrc"
            :class="[colorSchemeReverse, !shadow ? 'box-shadow' : shadow]"
            :src="imgSrc"
        >
        <video v-if="videoSrc && preload == 'none'"
            :class="[colorSchemeReverse, 'box-shadow']"
            muted playsinline
            :preload="preload" autoplay loop
            :src="videoSrc"
            @click.prevent="(e) => {
                if(e.target.paused) {
                    e.target.play();
                }
                else {
                    e.target.pause();
                }
            }"
        ></video>

        <video v-if="videoSrc && preload != 'none'"
            :class="[colorSchemeReverse, 'box-shadow']"
            muted playsinline
            :preload="!preload ? 'metadata' : preload" controls controlslist="nodownload nofullscreen"
            :src="videoSrc"
        ></video>

        <figcaption class="h5">
            <span v-if="caption">{{ caption }}</span>
            <br v-if="caption && captionEm">
            <b v-if="captionEm">{{ captionEm }}</b>
        </figcaption>
    </figure>
</template>

<script>
export default {
    props: ['colorSchemeReverse', 'shadow', 'imgSrc', 'videoSrc', 'preload', 'caption', 'captionEm']
}
</script>

<style>
figure {
    margin: 0;
}
figure img,
figure video {
    width: 100%;
    margin-bottom: calc(var(--gap-li) * 1.5);
    border-radius: var(--border-radius);
}
figcaption {
    letter-spacing: 0.3em;
    text-align: center;
    word-break: auto-phrase;
}
figcaption span {
    opacity: 0.8;
}
</style>