<template>
    <span>
        <i class="icon-rounded fill star" v-for="f in fullStar">star</i>
        <i class="icon-rounded" v-if="rating % 1 != 0">star_half</i>
        <i class="icon-rounded star" v-for="n in nullStar">star</i>
    </span>
</template>

<script>
export default {
    props: ['rating'],
    computed: {
        fullStar() {
            return Math.floor(this.rating / 1);
        },
        nullStar() {
            return (this.rating % 1 != 0) ? 4 - this.fullStar : 5 - this.fullStar;
        }
    }
}
</script>

<style scoped>
span {
    height: var(--icon-size);
    letter-spacing: calc(var(--gap-icon) / 2);
}
</style>