<template>
    <router-link v-for="(page, index) in pages"
        :key="index"
        :to="`/${page.url}`"
        :title= "`${page.display.toString().replaceAll(',', '')}作品`"
        class="h3 font-bold"
        @click.prevent="resetTop"
    ><span v-for="str in page.display">{{ str }}</span></router-link>
</template>

<script>
export default {
    inject: ['$pages'],
    data() {
        return {
            pages: []
        }
    },
    methods: {
        resetTop() {
            window.scrollTo({
                top: 0
            });
        }
    },
    created() {
        this.pages = this.$pages.getPages();
    }
}
</script>

<style scoped>
a {
    padding: calc(var(--line-height) / 2) 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    line-height: var(--line-height-xs);
    border-bottom: var(--border-width) solid;
    border-color: transparent;
    white-space: pre-wrap;
    opacity: 0.5;
}
a:hover, 
a.active {
    opacity: 1;
}
a.active {
    border-color: revert;
    transition: border-color var(--transition-color);
}
@media (max-width: 1280px) {
    a {
        flex-direction: column;
    }
}
</style>