<template>
    <section>
        <ul>
            <li v-for="skill in skills" class="to-fade">
                <div class="font-semibold">{{ skill.title }}</div>
                <p v-for="content in skill.contents">
                    <img v-if="content.icon && !content.iconL && !content.iconD" :src="require('../../assets/logo/' + content.icon)">
                    <img v-if="content.iconL" :src="require('../../assets/logo/' + content.iconL)">
                    <img v-if="content.iconD" :src="require('../../assets/logo/' + content.iconD)">
                    <span>{{ content.name }}</span>
                    <rating-stars v-if="content.rating" :rating="content.rating"></rating-stars>
                </p>
            </li>
        </ul>
    </section>
</template>

<script>
import data from '../../assets/data.json';
import RatingStars from '../RatingStars.vue';

export default {
    components: {
        RatingStars
    },
    props: ['colorSchemeReverse'],
    data() {
        return {
            skills: data.skills
        }
    }
}
</script>

