function fadeOnload(delay) {
    let fadeOnloads = [];
    const toFades = document.querySelectorAll('.to-fade');
    toFades.forEach(e => {
        if(e.offsetTop <= window.innerHeight) {
            fadeOnloads.push(e);
        }
    })

    fadeOnloads.forEach((e, i) => {
        setTimeout(() => {
            e.classList.add('faded');
        }, i * delay); // 每個元素延遲0.3秒
    });
}

function fadeOnscroll() {
    let fadeOnscrolls = [];
    const toFades = document.querySelectorAll('.to-fade');
    toFades.forEach(e => {
        if(e.offsetTop > window.innerHeight) {
            fadeOnscrolls.push(e);
        }
    })

    let observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                entry.target.classList.add('faded');
                observer.unobserve(entry.target);
            }
        });
    }, { threshold: 0.1 });

    fadeOnscrolls.forEach(e => {
        observer.observe(e);
    });
}



export default {
    fade(delayOnload) {
        setTimeout(() => {

            fadeOnload(delayOnload);
            fadeOnscroll();
            
        }, 0); // 延遲0.0秒
    }
}