<template>
    <header :class="[colorSchemeReverse, 'box-shadow']">
        <div>
            <i
                class="icon-rounded margin-right"
                @click.prevent="toggleNav"
            >menu</i>

            <nav>
                <nav-title
                    :color-scheme="colorScheme"
                ></nav-title>

                <nav-items
                    v-if="$device.width > 768"
                ></nav-items>
            </nav>

            <div
                :class="[colorScheme, 'overlay']"
                @click.prevent="toggleNav"
                v-if="showNav"
            ></div>

            <nav :class="['side', colorSchemeReverse, 'box-shadow']" v-if="showNav">
                <i 
                    class="icon-rounded"
                    @click.prevent="toggleNav"
                >close</i>

                <nav-items></nav-items>
            </nav>
        </div>
    </header>

    
</template>

<script>
import NavTitle from './Navbar/NavTitle.vue';
import NavItems from './Navbar/NavItems.vue';

export default {
    components: {
        NavTitle,
        NavItems
    },
    props: ['colorScheme', 'colorSchemeReverse'],
    data() {
        return {
            IsNavExpanded: false
        }
    },
    computed: {
        showNav() {
            return this.$device.width <= 768 ? this.IsNavExpanded : false;
        }
    },
    methods: {
        toggleNav() {
            this.IsNavExpanded = !this.IsNavExpanded;
        }
    }
}
</script>

<style scoped>
header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
}
header > div {
    width: var(--content-width);
    height: var(--header-height);
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
i {
    margin-right: var(--gap-column);
    display: none;
    font-size: var(--icon-size);
    line-height: var(--header-height);
    cursor: pointer;
}
nav {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
nav.side {
    position: fixed;
    top: 0;
    left: 0;
    width: max(calc(var(--header-height) * 1.2), calc((100% - var(--content-width)) / 2 + var(--line-height) + var(--gap-column)));
    height: calc(100vh - var(--header-height) * 2);
    padding: var(--header-height) 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
div.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}
nav.side i {
    position: absolute;
    top: 0;
    right: 0;
}
@media (max-width: 768px) {
    i {
        display: inline-block;
    }
    nav {
        justify-content: center;
    }
}
</style>