<template>
    <button v-if="scrollTop > 0"
        :class="[colorScheme, 'icon-rounded', 'drop-shadow reverse', 'overlay']"
        @click.prevent="backToTop"
    >keyboard_arrow_up</button>
</template>

<script>
export default {
    props: ['colorScheme'],
    data() {
        return {
            scrollTop: 0
        }
    },
    methods: {
        backToTop() {
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            });
        },
        onScroll() {
            this.scrollTop = document.documentElement.scrollTop;
        }
    },
    mounted() {
        window.addEventListener('scroll', this.onScroll);
        this.onScroll();
    },
    beforeUnmount () {
        window.removeEventListener('scroll', this.onScroll);
    }
}
</script>

<style scoped>
button {
    border: var(--button-border-width) solid;
    border-color: inherit;
}
</style>