<template>
    <main>
        <section>
            <div>
                <contact-col-l class="to-fade"
                    :color-scheme-reverse="colorSchemeReverse"
                ></contact-col-l>
                <contact-col-r class="to-fade"
                    :active-index="activeIndex"
                    :onclick="(index) => activeIndex = index"
                ></contact-col-r>
            </div>
        </section>

        <component :is="activeComp" class="h5"
            :color-scheme-reverse="colorSchemeReverse"
        ></component>
    </main>
</template>

<script>
import ContactColL from '../components/AboutZenys/ContactColL.vue';
import ContactColR from '../components/AboutZenys/ContactColR.vue';
import ContentItem0 from '../components/AboutZenys/ContentItem0.vue';
import ContentItem1 from '../components/AboutZenys/ContentItem1.vue';
import ContentItem2 from '../components/AboutZenys/ContentItem2.vue';

export default {
    components: {
        ContactColL,
        ContactColR,
        ContentItem0,
        ContentItem1,
        ContentItem2
    },
    props: ['colorSchemeReverse'],
    data() {
        return {
            activeIndex: 0
        }
    },
    computed: {
        activeComp() {
            return 'content-item-' + this.activeIndex;
        }
    },
    mounted() {
        this.$transitions.fade(300);
    },
    beforeUpdate() {
        this.$transitions.fade(150);
    }
}
</script>

<style scoped>
div {
    height: fit-content;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: 70% auto;
}

/* col-l & col-r */
:deep(header > div),
:deep(nav) {
    height: var(--photo-size);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
:deep(header > div > *),
:deep(nav a) {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: var(--gap-icon);
}
@media (max-width: 1024px) {
    div {
        grid-template-columns: 100%;
    }
    :deep(nav) {
        height: calc(var(--gap-row) + var(--line-height) + var(--padding-style));
        flex-direction: row;
        justify-content: space-around;
    }
}
@media (max-width: 768px) {
    :deep(nav) {
        height: calc(var(--gap-row) + var(--line-height) * 2 + var(--padding-style));
    }
    :deep(nav a) {
        flex-direction: column;
        gap: 0;
    }
}

/* content-item-0 & content-item-1 & content-item-2 */
:deep(ul) {
    margin: 0;
    padding-inline-start: var(--text-indent);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: space-between;
    justify-content: space-between;
}
:deep(li) {
    min-width: var(--photo-size);
}
:deep(li *) {
    min-height: var(--icon-size);
}
:deep(li > *) {
    margin: 0 0 var(--gap-li);
}
:deep(li p),
:deep(li > div:has(.label)) {
    display: flex;
    flex-direction: row;
    gap: var(--gap-icon);
}
@media (max-width: 1024px) {
    :deep(ul) {
        align-content: space-around;
    }
}
@media (max-width: 480px) {
    :deep(li > div:has(.label)) {
        flex-direction: column;
        gap: 0;
    }
}
</style>