<template>
    <main>
        <header v-if="page">
            <h1 :class="['font-bold', colorSchemeReverse, 'to-fade']" v-if="page.display">
                {{ page.display.toString().replaceAll(',', '') }}作品
            </h1>
        </header>

        <component
            :is="activeComp"
            :color-scheme-reverse="colorSchemeReverse"
        ></component>
    </main>
</template>

<script>
import FrontendDevelopment from './FrontendDevelopment.vue';
import LINEBot from './LINEBot.vue';
import GameDevelopment from './GameDevelopment.vue';
import ThreeDimensionalModeling from './ThreeDimensionalModeling.vue';
import GraphicDesign from './GraphicDesign.vue';
import MotionGraphicDesign from './MotionGraphicDesign.vue';

export default {
    components: {
        FrontendDevelopment,
        LINEBot,
        GameDevelopment,
        ThreeDimensionalModeling,
        GraphicDesign,
        MotionGraphicDesign
    },
    props: ['to', 'colorSchemeReverse'],
    inject: ['$pages'],
    data() {
        return {
            page: null,
            activeContent: ''
        }
    },
    computed: {
        activeComp() {
            return this.to;
        }
    },
    watch: {
        to(newTo, oldTo) {
            this.page = this.$pages.getPage(newTo);
        }
    },
    created() {
        this.page = this.$pages.getPage(this.to);
    },
    mounted() {
        this.$transitions.fade(300);
    },
    beforeUpdate() {
        this.$transitions.fade(150);
    }
}
</script>

<style scoped>
h1 {
    width: fit-content;
    height: fit-content;
    padding: 0 calc(var(--padding-style) * 1.5);
    border-radius: var(--border-radius);
}
header,
article,
:deep(section) {
    display: flex;
    flex-direction: column;
    gap: var(--gap-row);
}
:deep(section:not(:first-child)) {
    gap: calc(var(--gap-row) / 2);
}
:deep(a) {
    margin-bottom: var(--padding-style);
    text-decoration: underline calc(var(--border-width) / 3);
    text-underline-offset: var(--padding-style);
}
:deep(div p:first-child) {
    margin-top: 0;
}
:deep(div p:last-child) {
    margin-bottom: 0;
}

/* TextBox & ComparisonSection */
:deep(h2),
:deep(h3) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    line-height: var(--line-height-s);
    word-break: auto-phrase;
    white-space: pre-wrap;
}
:deep(h2) {
    letter-spacing: 0.3em;
}
:deep(h3) {
    letter-spacing: 0.2em;
}
</style>