import { reactive } from 'vue';

const device = {
    xs: 480,
    s: 768,
    m: 1024,
    l: 1280,
    xl: 9999
}

let deviceInfo = {
    size: '',
    width: 0
}

const calcWidth = (width) => {
    return Object.keys(device).find(key => device[key] >= width);
}

// const calcHeight = () => {
    
// }

function onResize() {
    const width = window.innerWidth;
    deviceInfo.size = calcWidth(width);
    deviceInfo.width = width;
}

window.addEventListener('resize', onResize);
onResize();

export default deviceInfo = reactive({
    size: deviceInfo.size,
    width: deviceInfo.width
})