<template>
    <navbar
        :color-scheme="colorScheme"
        :color-scheme-reverse="colorSchemeReverse"
    ></navbar>

    
    <router-view
        :color-scheme-reverse="colorSchemeReverse"
    ></router-view>
    
    <call-to-action
            :is-dark-mode="isDarkMode"
            :color-scheme="colorScheme"
            :color-scheme-reverse="colorSchemeReverse"
            :toggle-mode="toggleMode"
    ></call-to-action>
</template>

<script>
import Navbar from './components/Navbar.vue';
import PageViewer from './views/PageViewer.vue';
import CallToAction from './components/CallToAction.vue';

export default {
    name: 'App',
    components: {
        Navbar,
        PageViewer,
        CallToAction
    },
    data() {
        return {
            isDarkMode: true
        }
    },
    computed: {
        colorScheme() {
            return {
                'bg-dark': this.isDarkMode,
                'bg-light': !this.isDarkMode
            }
        },
        colorSchemeReverse() {
            return {
                'bg-dark': !this.isDarkMode,
                'bg-light': this.isDarkMode
            }
        }
    },
    methods: {
        // for ModeToggler
        toggleMode() {
            this.isDarkMode = !this.isDarkMode;
            this.setMode();
        },
        setMode() {
            localStorage.setItem('isDarkMode', this.isDarkMode);
        },
        getMode() {
            if(localStorage.getItem('isDarkMode')) {
                this.isDarkMode = localStorage.getItem('isDarkMode') == 'true' ? true : false;
            }
        }
    },
    created() {
        this.getMode();
    },
    mounted() {
        this.$transitions.fade(300);
    },
    beforeUpdate() {
        this.$transitions.fade(150);
    }
}
</script>