<template>
    <nav>
        <a v-for="(tab, index) in tabs"
            :key="index"
            :index="index"
            :class="[$device.width > 768 ? 'h3' : 'h4', 'font-medium', index == activeIndex ? 'active' : '']"
            @click.prevent="onclick(index)"
        >
            <i class="icon-rounded fill">{{ tab.icon }}</i>
            <span>{{ tab.title }}</span>
        </a>
    </nav>
</template>

<script>
export default {
    props: ['activeIndex', 'onclick'],
    data() {
        return {
            hasSmallIcon: true,
            tabs: [
                {
                    icon: 'person',
                    title: '關於我'
                },
                {
                    icon: 'auto_stories',
                    title: '技能與專長'
                },
                {
                    icon: 'apartment',
                    title: '工作經歷'
                }
            ]
        }
    }
}
</script>

<style scoped>
nav {
    width: 100%;
    align-items: flex-end;
}
nav a {
    opacity: 0.5;
}
nav a:hover,
nav a.active {
    opacity: 1;
}
nav a.active {
    padding-bottom: var(--padding-style);
    border-bottom: calc(var(--border-width) / 2) solid;
    border-color: revert;
    animation: bounce 0.5s ease;
}
@media (max-width: 1024px) {
    nav a {
        padding-bottom: var(--padding-style);
    }
}
@media (max-width: 768px) {
    nav a,
    nav a.active {
        padding-bottom: 0;
        border: none;
    }
    nav a i {
        font-size: 1.5em;
    }
}
</style>