<template>
    <header ref="colL">
        <img :class="colorSchemeReverse" src="../../assets/photo.png" ref="img">
        <div :class="colLHeight > imgHeight ? 'align-items-center' : ''">
            <div>
                <h1 class="font-semibold">{{ infos.name }}</h1>
                <h2 class="font-medium">{{ infos.nameEng }}</h2>
            </div>
            <a class="h3" :href="`tel: ${infos.phoneCC}`">
                <i class="icon-rounded fill">phone_iphone</i>
                <span class="font-medium">{{ infos.phone }}</span>
            </a>
            <a class="h3" :href="`mailto: ${infos.mail}`">
                <i class="icon-rounded">mail</i>
                <span class="font-medium">{{ infos.mail }}</span>
            </a>
        </div>
    </header>
</template>

<script>
import data from '../../assets/data.json';

export default {
    props: ['colorSchemeReverse'],
    data() {
        return {
            infos: data.infos,
            resizeObserver: null,
            colLHeight: 0,
            imgHeight: 0
        }
    },
    methods: {
        onResize() {
            this.colLHeight = this.$refs.colL.offsetHeight;
            this.imgHeight = this.$refs.img.offsetHeight;
        }
    },
    mounted() {
        this.resizeObserver = new ResizeObserver(this.onResize);
        this.resizeObserver.observe(this.$refs.colL);
        this.onResize();
    },
    beforeUnmount () { 
        this.resizeObserver.unobserve(this.$refs.colL);
    }
}
</script>

<style scoped>
header {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    gap: var(--gap-column);
}
header img {
    width: var(--photo-size);
    height: var(--photo-size);
    border-width: var(--border-width);
    border-style: solid;
    border-radius: var(--border-radius);
}
@media (max-width: 1024px) {
    header {
        justify-content: center;
    }
}
</style>